#graph {
  width: 100%;
  height: 400px;
  border: 3px solid black;
  display: block;
  z-index: 0;
}

#inputModelID {
  width: 50px;
}

.badge-larger {
  font-size: 16px;
  max-width: 80%;
}

.badge-maxwidth {
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
}

[data-toggle~="collapse"] {
  cursor: pointer;
}

.buttonIcon {
  width: 20px;
  height: 20px;
}




